@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;1,700&display=swap");
body {
  height: 100vh;
  background-color: whitesmoke;
  font-family: 'Poppins', sans-serif;
  overflow: hidden;
}

.card-container {
  position: relative;
}

#img {
  position: absolute;
  width: 80%;
  height: 665px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  left: 50%;
  -webkit-filter: opacity(5.4) drop-shadow(0 0 0 red);
          filter: opacity(5.4) drop-shadow(0 0 0 red);
  z-index: 0;
}

.card {
  background-color: #ff7a7a;
  padding: 80px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 70%;
  min-height: 400px;
  margin: 30px auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.left-part {
  -webkit-box-flex: 5;
      -ms-flex: 5;
          flex: 5;
  color: white;
}

.left-part h1 {
  line-height: 40px;
  font-weight: 700;
  font-size: 2.4em;
}

.left-part p {
  font-size: 15px;
}

.right-part {
  -webkit-box-flex: 5;
      -ms-flex: 5;
          flex: 5;
  text-align: center;
  -webkit-transform: translateX(5%);
          transform: translateX(5%);
}

.right-part input {
  height: 40px;
  border-radius: 5px;
  border: 0.5px solid #b9b6d3;
  color: black;
  padding-left: 15px;
  width: 340px;
  -webkit-user-select: auto;
     -moz-user-select: auto;
      -ms-user-select: auto;
          user-select: auto;
  margin: 15px 0;
}

.right-part #btn {
  background-color: #38cc8c;
  color: white;
  border: 0.5px solid #38cc8c;
  font-weight: 700;
}

.right-part .text-container {
  background-color: #6055a5;
  padding: 1px;
  margin-bottom: 30px;
  border-radius: 8px;
  color: white;
}

.right-part .text-container h3 {
  font-size: 1em;
  font-weight: 400;
}

.right-part .text-container h3 span {
  font-weight: 700;
}

.right-part .form-container {
  background-color: white;
  padding: 15px;
  border-radius: 8px;
}

.right-part .form-container p {
  width: 90%;
  margin: 0 auto;
  color: #b9b6d3;
  font-size: 0.65em;
  padding-bottom: 10px;
}

.right-part .form-container p span {
  color: #ff7a7a;
  font-weight: 700;
}
