$Red: hsl(0, 100%, 74%) ;
$Green: hsl(154, 59%, 51%);
$Blue: hsl(248, 32%, 49%);
$DarkBlue: hsl(249, 10%, 26%) ;
$GrayishBlue: hsl(246, 25%, 77%);
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;1,700&display=swap');



body {
    height: 100vh;
    background-color: whitesmoke;
    font-family: 'Poppins', sans-serif;
    overflow: hidden;
}
.card-container {
    position: relative;
   
}
#img {
   position: absolute;
   width: 80%;
   height: 665px;
   transform: translateX(-50%);
   left: 50%;
   filter: opacity(5.4) drop-shadow(0 0 0 red);
   z-index: 0;
}
.card {
    background-color: hsl(0, 100%, 74%);
    padding: 80px;
    align-items: center;
    width: 70%;
    min-height: 400px;
    margin: 30px auto;
    display: flex;
}
.left-part {
    flex: 5;
    color: white;
    h1 {
        line-height: 40px;
        font-weight: 700;
        font-size: 2.4em;
    }
    p {
        font-size: 15px;
    }
}
.right-part {
    flex: 5;
    text-align: center;
    transform: translateX(5%);
    input {
        height: 40px;
        border-radius: 5px;
        border: 0.5px solid $GrayishBlue;
        color: black;
        padding-left: 15px;
        width: 340px;
        user-select: auto;
        margin: 15px 0;
       
    }
    #btn {
        background-color: $Green;
        color: white;
        border: 0.5px solid $Green;
        font-weight: 700;
    }
    .text-container {
        background-color: hsl(248, 32%, 49%);
        
        padding: 1px;
        margin-bottom: 30px;
        border-radius: 8px;
        color: white;
        h3 {
            font-size: 1em;
            font-weight: 400;
            span {
                font-weight: 700;
            }
            
        }
    }
    .form-container {
        background-color: white;
        padding: 15px;
        border-radius: 8px;
        p {
            width: 90%;
            margin: 0 auto;
            color: $GrayishBlue;
            font-size: 0.65em;
            padding-bottom: 10px;
            span {
                color:$Red;
                font-weight: 700;
            }
        }
    }
}
